<template>
    <div id="index">
        <!-- 页头 -->
        <div class="container header">
            <div class="jumbotron">
                <h1 class="display-4">欢迎来到图书商城</h1>
                <hr class="my-4">
                <h5>这里有各式各样的书籍任你挑选</h5>
                <a class="btn btn-primary btn-lg" href="#searchBox" role="button">浏览</a>
            </div>
        </div>

        <!-- 搜索框 -->
        <div class="container searchBox">
            <h3 id="searchBox" class="text-center font-weight-normal text-info">图书搜索</h3>
            <div class="row">
                <div class="col-lg-6 offset-lg-3 col-md-8 offset-md-2 col-sm-8 offset-sm-2">
                    <div class="input-group">
                        <input v-model="search" type="text" class="form-control" placeholder="请输入图书名">
                        <span class="input-group-btn">
                            <button @click="showSearchBook" class="btn btn-primary" type="button">查询</button>
                        </span>
                    </div><!-- /input-group -->
                </div><!-- /.col-lg-6 -->
            </div>
        </div>

        <!-- 书籍列表 -->
        <div class="container">
        <hr>
            <div class="row">
                <div 
                    class="book col-lg-3"
                    v-for="book in bookList"
                    :key="book.id"    
                >
                    <div class="thumbnail">
                        <img class="rounded" :src="book.image">
                        <div class="caption">
                            <p>{{book.bookName}}</p>
                            <p>
                                <button @click="showDialog(book)" class="btn btn-primary">购买</button>
                                <router-link 
                                    :to="{
                                        path:'/bookDetails',
                                        query:{book}
                                    }" 
                                    class="btn btn-secondary" 
                                    role="button"
                                >
                                    详情
                                </router-link>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <el-dialog
            title="添加至购物车"
            :visible.sync="marketDialogVisible"
            width="40%"
            :before-close="handleClose"
        >
            <h2 class="title">图书信息</h2>
            <el-descriptions 
                :border="true"
                :column="1"
            >
                <el-descriptions-item label="书名">{{operateBook.bookName}}</el-descriptions-item>
                <el-descriptions-item label="单价">{{operateBook.price}}</el-descriptions-item>
                <el-descriptions-item label="数量">
                    <el-input-number 
                        v-model="addToMarketBookNumber" 
                        :min="1" 
                    >
                    </el-input-number>
                </el-descriptions-item>
                <el-descriptions-item label="总金额">{{totalMoney}}</el-descriptions-item>
            </el-descriptions>
            <span slot="footer" class="dialog-footer">
                <el-row type="flex" justify="center" align="middle">
                    <el-button @click="marketDialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="addToMarket">确 定</el-button>
                </el-row>
            </span>
        </el-dialog>

        <el-row type="flex" justify="center" align="middle">
            <el-pagination
                background
                layout="prev, pager, next"
                :total="bookTotalNumber"
                :current-page.sync="currentPage"
                @current-change="handleCurrentChange"
                :page-size="8"
            >
            </el-pagination>
        </el-row>
        <router-view></router-view>
    </div>
</template>

<script>
    import qs from 'qs'
    import {mapState, mapMutations} from 'vuex'
    export default {
        name: 'Index',
        data() {
            return {
                bookList:[],
                search: '',
                currentPage: 1,
                bookTotalNumber: 0,
                operateBook:{},
                marketDialogVisible: false,
                addToMarketBookNumber: 1,
            }
        },
        computed:{
            ...mapState(['marketId', 'username']),
            totalMoney(){
                return this.operateBook.price * this.addToMarketBookNumber
            }
        },
        mounted() {
            //加载书籍总数量bookTotalNumber
            this.$axios.get('/book/getTotalNumber').then(
                response => {
                    this.bookTotalNumber = response.data
                },
                error => {
                    console.log(error.message)
                }
            )

            //加载当前页的书籍列表
            this.$axios.get(`/book/page/${this.currentPage}/8`).then(
                response => {
                    this.bookList = response.data
                },
                error => {
                    console.log(error.message)
                }
            )
        },
        methods: {
            ...mapMutations(['ADD_MARKET_TOTAL_NUMBER']),
            showSearchBook(){
                if(this.search.trim === '')
                {
                    this.$message({
                        message: '请输入书名进行查询!',
                        type: 'warning',
                        duration: '2000',
                        center: true
                    });
                } 
                else
                {
                    this.$axios.post('/book/getBookByBookNameNumber', qs.stringify({ bookName:this.search.trim()})).then(
                        response => {
                            this.bookTotalNumber = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )

                    //加载当前页的书籍列表
                    this.$axios.post(`/book/getBookListByBookName/${this.currentPage}/8`, qs.stringify({ bookName:this.search.trim()})).then(
                        response => {
                            this.bookList = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )
                }
            },
            handleCurrentChange(){
                if(this.search.trim() === '')
                {
                    //加载书籍总数量bookTotalNumber
                    this.$axios.get('/book/getTotalNumber').then(
                        response => {
                            this.bookTotalNumber = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )

                    //加载当前页的书籍列表
                    this.$axios.get(`/book/page/${this.currentPage}/8`).then(
                        response => {
                            this.bookList = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )
                }
                else
                {   
                    this.$axios.post('/book/getBookByBookNameNumber', qs.stringify({ bookName:this.search.trim()})).then(
                        response => {
                            this.bookTotalNumber = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )

                    //加载当前页的书籍列表
                    this.$axios.post(`/book/getBookListByBookName/${this.currentPage}/8`, qs.stringify({ bookName:this.search.trim()})).then(
                        response => {
                            this.bookList = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )
                }
            },
            showDialog(book){
                if(this.username !== '')
                {
                    this.operateBook = book
                    this.marketDialogVisible = true
                }else
                {
                    this.$message({
                        message: '请先登录再进行购买!',
                        type: 'warning',
                        duration: '2000',
                        center: true
                    });
                    if(this.$route.path !== '/login')
                    {
                        this.$router.push('/login')
                    }
                }
            },
            handleClose(done) {
                this.$confirm('确认关闭?')
                .then(_ => {
                    done();
                })
                .catch(_ => {});
            },
            addToMarket(){
                this.$axios.post('/market/getMarketListByMarketIdAndBookId',qs.stringify({
                    marketId: this.marketId,
                    bookId: this.operateBook.id,
                })).then(
                    response => {
                        //该用户购物车不存在该书籍
                        if(response.data)
                        {
                            this.$axios.post('/market/addToMarket',qs.stringify({
                                marketId: this.marketId,
                                bookId: this.operateBook.id,
                                number: this.addToMarketBookNumber
                            })).then(
                                res => {
                                     console.log(res.data)
                                },
                                error => {
                                    console.log(error.message)
                                }
                            )
                        }
                        //修改书籍数量即可
                        else
                        {
                            const newNumber = response.data[0].number + this.addToMarketBookNumber
                            console.log(newNumber)
                            this.$axios.post('/market/updateMarket',qs.stringify({
                                marketId: this.marketId,
                                bookId: response.data[0].bookId,
                                number: newNumber
                            })).then(
                                res => {
                                    console.log(res.data)
                                },
                                error => {
                                    console.log(error.message)
                                }
                            )
                        }
                        this.$notify({
                            title: '成功',
                            message: "添加至购物车成功!",
                            type: 'success',
                            duration: 2000
                        });

                        this.marketDialogVisible = false
                        this.addToMarketBookNumber = 1
                        this.ADD_MARKET_TOTAL_NUMBER(this.addToMarketBookNumber)
                    },
                    error => {
                        console.log(error.message)
                    }
                )
            }
        },
    }
</script>

<style scoped>
    img {
        width: 200px;
        height: 200px;
    }

    .header{
        margin-top: 50px;
    }

    .searchBox{
        margin-top: 80px;
    }

    .searchBox .row{
        margin-top: 30px;
        margin-bottom: 50px;
    }

    .book{
        text-align: center;
        font-size: large;
        margin-top: 20px;
    }

    .bookPage{
        margin: 30px auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .el-pagination{
        margin-top: 30px;
        margin-bottom: 100px;
    }

    h2{
        margin-top: 20px;
        margin-bottom: 20px;

    }
</style>