<template>
  <div id="bookDetails">
    <h2 class="title">图书详情</h2>
    <el-row>
        <el-col :span="12">
            <el-descriptions 
                class="main"
                :border="true"
                :column="1"
            >
                <el-descriptions-item label="书名">{{bookName}}</el-descriptions-item>
                <el-descriptions-item label="作者">{{author}}</el-descriptions-item>
                <el-descriptions-item label="单价">{{price}}</el-descriptions-item>
                <el-descriptions-item label="描述">{{description}}</el-descriptions-item>
                <el-descriptions-item label="操作">
                    <el-row type="flex" justify="center" align="middle">
                        <el-button @click="showDialog" type="primary" plain>购买</el-button>
                    </el-row>
                </el-descriptions-item>
            </el-descriptions>
        </el-col>

        <el-col :span="12">
            <el-image 
                style="width: 300px; height: 300px"
                :src="image" 
            >
            </el-image>
        </el-col>
    </el-row>

    <el-dialog
        title="添加至购物车"
        :visible.sync="marketDialogVisible"
        width="40%"
        :before-close="handleClose"
    >
        <h2 class="title">图书信息</h2>
        <el-descriptions 
            :border="true"
            :column="1"
        >
            <el-descriptions-item label="书名">{{bookName}}</el-descriptions-item>
            <el-descriptions-item label="单价">{{price}}</el-descriptions-item>
            <el-descriptions-item label="数量">
                <el-input-number 
                    v-model="addToMarketBookNumber" 
                    :min="1" 
                >
                </el-input-number>
            </el-descriptions-item>
            <el-descriptions-item label="总金额">{{totalMoney}}</el-descriptions-item>
        </el-descriptions>
        <span slot="footer" class="dialog-footer">
            <el-row type="flex" justify="center" align="middle">
                <el-button @click="marketDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="addToMarket">确 定</el-button>
            </el-row>
        </span>
    </el-dialog>
  </div>
</template>

<script>
    import qs from 'qs'
    import {mapState, mapMutations} from 'vuex'
    export default {
        name:'BookDetails',
        data() {
            return {
                marketDialogVisible: false,
                addToMarketBookNumber: 1,
            }
        },
        computed:{
            ...mapState(['marketId', 'username']),
            id(){
                return this.$route.query.book.id
            },
            bookName(){
                return this.$route.query.book.bookName
            },
            author(){
                return this.$route.query.book.author
            },
            price(){
                return this.$route.query.book.price
            },
            image(){
                return this.$route.query.book.image
            },
            description(){
                return this.$route.query.book.description
            },
            totalMoney(){
                return this.price * this.addToMarketBookNumber
            }
        },
        methods: {
            ...mapMutations(['ADD_MARKET_TOTAL_NUMBER']),
            handleClose(done) {
                this.$confirm('确认关闭?')
                .then(_ => {
                    done();
                })
                .catch(_ => {});
            },
            showDialog(){
                if(this.username !== '')
                {
                    this.marketDialogVisible = true
                }else
                {
                    this.$message({
                        message: '请先登录再进行购买!',
                        type: 'warning',
                        duration: '2000',
                        center: true
                    });
                    if(this.$route.path !== '/login')
                    {
                        this.$router.push('/login')
                    }
                }
            },
            addToMarket(){
                this.$axios.post('/market/getMarketListByMarketIdAndBookId',qs.stringify({
                    marketId: this.marketId,
                    bookId: this.id,
                })).then(
                    response => {
                        console.log(response.data)
                        //该用户购物车不存在该书籍
                        if(response.data)
                        {
                            this.$axios.post('/market/addToMarket',qs.stringify({
                                marketId: this.marketId,
                                bookId: this.id,
                                number: this.addToMarketBookNumber
                            })).then(
                                res => {
                                     console.log(res.data)
                                },
                                error => {
                                    console.log(error.message)
                                }
                            )
                        }
                        //修改书籍数量即可
                        else
                        {
                            const newNumber = response.data[0].number + this.addToMarketBookNumber
                            console.log(newNumber)
                            this.$axios.post('/market/updateMarket',qs.stringify({
                                marketId: this.marketId,
                                bookId: this.id,
                                number: newNumber
                            })).then(
                                res => {
                                    console.log(res.data)
                                },
                                error => {
                                    console.log(error.message)
                                }
                            )
                        }
                        this.$notify({
                            title: '成功',
                            message: "添加至购物车成功!",
                            type: 'success',
                            duration: 2000
                        });

                        this.marketDialogVisible = false
                        this.addToMarketBookNumber = 1
                        this.ADD_MARKET_TOTAL_NUMBER(this.addToMarketBookNumber)
                    },
                    error => {
                        console.log(error.message)
                    }
                )
            }
        },

    }
</script>

<style scoped>
    .title{
        margin: 20px auto;
        color: #409EFF;
        font-size: 24px;
    }

    .main {
        margin-left: 80px;
    }

    .el-image {
        position: absolute;
        right: 170px;
        top: 80px;
    }
</style>