<template>
    <div id="login">
        <el-form ref="form" :model="form" size="medium">
            <h2 class="title">找回密码</h2>
            <el-row type="flex" justify="center" align="middle">
                <el-col :span="6">
                    <el-form-item
                        label="账号"
                        prop="username"
                        :rules="[
                            { required: true, message: '账号不能为空'}
                        ]"
                    >
                        <el-input v-model="form.username"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="6">
                    <el-form-item
                        inline-message="true"
                        label="手机号"
                        prop="phone"
                        :rules="[
                            { required: true, message: '手机号不能为空'}
                        ]"
                    >
                        <el-input v-model="form.phone"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            

            <el-form-item>
                <el-row type="flex" justify="center" align="middle">
                    <el-button type="primary" @click="findPassword('form')">找回密码</el-button>
                    <el-button @click="resetForm('form')">重置</el-button>
                </el-row>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import qs from 'qs'
    export default {
        name:'FindPassword',
        data() {
            return {
                form: {
                    username: '',
                    phone: '',
                }
            }
        },
        methods: {
            findPassword(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$axios.post('/user/getUserByUsernameAndPhone',qs.stringify({
                                username: this.form.username,
                                phone: this.form.phone
                        })).then(
                            response => {
                                if(response.data)
                                {
                                    this.$axios.post('/user/resetPassword',qs.stringify({
                                            username: this.form.username
                                    })).then(
                                        res => {
                                            this.$notify({
                                                title: '成功',
                                                message: "用户" + this.form.username + "的密码已重置为123456!",
                                                type: 'success',
                                                duration: 2000
                                            });
                                            
                                            this.$router.push('/login')
                                        },
                                        error => {
                                            console.log(error.message)
                                        }
                                    )
                                }
                                else
                                {
                                    this.$message({
                                        showClose: true,
                                        message: `用户名或手机号错误`,
                                        type: 'error',
                                        duration: '2000',
                                        center: true
                                    });
                                }
                            },
                            error => {
                                console.log(error.message)
                            }
                        )
                    } else {
                        return false
                    }
                })
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        },
    }
</script>

<style scoped>
    h2{
        margin-top: 110px;
        text-align: center;
        color: #409EFF;
        font-size: 24px;
    }
</style>