import VueRouter from 'vue-router'
import Index from '../components/Index'
import BookDetails from '../components/BookDetails'
import Login from '../components/Login'
import FindPassword from '../components/FindPassword'
import Register from '../components/Register'
import PersonalInfo from '../components/PersonalInfo'
import PasswordUpdate from '../components/PasswordUpdate'
import AddBook from '../components/AddBook'
import BookList from '../components/BookList'
import OrderList from '../components/OrderList'
import BookLeftNav from '../pages/BookLeftNav'

const router = new VueRouter({
    mode:"history",

    routes:[
        {
            path:'/',
            component:Index
        },
        {
            path:'/bookDetails',
            component:BookDetails,
            meta:{title: '图书详情'},
        },
        {
            path:'/login',
            component:Login,
            meta:{title: '登录'},
        },
        {
            path:'/findPassword',
            component:FindPassword,
            meta:{title: '找回密码'},
        },
        {
            path:'/register',
            component:Register,
            meta:{title: '注册'},
        },
        {
            path:'/bookLeftNav',
            component:BookLeftNav,
            meta:{title: '用户中心', isAuth:true},
            children:[
                {
                    path:'personalInfo',
                    component:PersonalInfo,
                    meta:{title: '个人信息', isAuth:true},
                },
                {
                    path:'passwordUpdate',
                    component:PasswordUpdate,
                    meta:{title: '修改密码', isAuth:true},
                },
                {
                    path:'addBook',
                    component:AddBook,
                    meta:{title: '新增书籍', isAuth:true},
                },
                {
                    path:'bookList',
                    component:BookList,
                    meta:{title: '书籍列表', isAuth:true},
                },
                {
                    path:'orderList',
                    component:OrderList,
                    meta:{title: '订单列表', isAuth:true},
                },
            ]
        },
    ]
})


//前置守卫 初始化时或跳转路由前触发
router.beforeEach((to, from, next) => {
    if(to.meta.isAuth){
        if(sessionStorage.getItem('username') === null)
        {
            next('/login')
        }
        else
        {
            next()
        }
    }
    else
    {
        next()
    }
})

//后置守卫 初始化时或跳转路由后触发
router.afterEach((to, from) => {
    document.title = to.meta.title || '图书商城'
})


export default router
