<template>
    <div id="bookList">
        <el-row type="flex" justify="center" align="middle">
            <h2 class="title">图书列表</h2>
        </el-row>

        <el-row type="flex" justify="end">
            <el-col :span="8" :pull="1">
                <el-input
                    v-model="search"
                    size="mini"
                    placeholder="输入书名搜索"
                    @change="showSearchBook"
                >
                </el-input>
            </el-col>
        </el-row>

        <el-table
            :data="bookList"
            stripe
            style="width: 95%"
            height="592"
        >
            <el-table-column
                prop="id"
                label="编号"
                width="100"
            >
            </el-table-column>
            <el-table-column
                prop="bookName"
                label="书名"
                width="100"
            >
            </el-table-column>
            <el-table-column
                prop="author"
                label="作者"
                width="70"
            >
            </el-table-column>
            <el-table-column
                prop="image"
                label="图片"
                width="150"
            >
                <template slot-scope="scope">
                    <el-image
                        style="width: 100px; height: 100px"
                        :src="scope.row.image"
                        fit="fill">
                    </el-image>
                </template>
            </el-table-column>
            <el-table-column
                prop="price"
                label="单价"
                width="70"
            >
            </el-table-column>
            <el-table-column
                label="操作"
                width="150"
            >
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        type="primary"
                        @click="handleEdit(scope.$index, scope.row)"
                    >
                        修改
                    </el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)"
                    >
                        删除
                    </el-button>
                </template>
            </el-table-column> 
        </el-table>

        <el-dialog
            title="提示"
            :visible.sync="showUpdateDialog"
            width="50%"
            :center="true"
            :before-close="handleClose"
        >
             <el-row type="flex" justify="center" align="middle">
                <h2 class="title">修改书籍</h2>
            </el-row>
            <el-form ref="operateBook" :model="operateBook" label-width="80px" :hide-required-asterisk="true">
                <el-row type="flex" justify="center" align="middle">
                    <el-col :span="18">
                        <el-form-item 
                            prop="bookName"
                            label="图书名称"
                            :rules="[
                                { required: true, message: '请输入图书名称'},
                            ]"
                        >
                            <el-input v-model="operateBook.bookName"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-row type="flex" justify="center" align="middle">
                    <el-col :span="18">
                        <el-form-item 
                            prop="author"
                            label="作者"
                            :rules="[
                                { required: true, message: '请输入图书作者'},
                            ]"
                        >
                            <el-input v-model="operateBook.author"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                
                <el-row type="flex" justify="center" align="middle">
                    <el-col :span="18">
                        <el-form-item 
                            prop="price"
                            label="图书单价"
                        >
                            <el-input-number 
                                v-model="operateBook.price" 
                                :min="1" 
                                :max="200"
                            >
                            </el-input-number>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-row type="flex" justify="center" align="middle">
                    <el-col :span="18">
                        <el-form-item
                            prop="description"
                            label="图书简介"
                            :rules="[
                                { required: true, message: '请输入图书简介'},
                            ]"
                        >
                            <el-input
                                type="textarea" 
                                v-model="operateBook.description"
                                :autosize="{ minRows: 4, maxRows: 8}"
                                maxlength="500"
                                show-word-limit
                            >
                            </el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-row type="flex" justify="center" align="middle">
                    <el-col :span="18">
                        <el-form-item>
                            <el-button type="primary" @click="updateBook('operateBook')">修改</el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
        </el-dialog>

        <el-dialog
            title="提示"
            :visible.sync="showDeleteDialog"
            width="30%"
            :center="true"
        >
            <span>你确定要删除书籍{{operateBook.bookName}}吗?</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="showDeleteDialog = false">取 消</el-button>
                <el-button type="primary" @click="deleteBook">确 定</el-button>
            </span>
        </el-dialog>

        <el-row type="flex" justify="center" align="middle">
            <el-pagination
                background
                layout="prev, pager, next"
                :total="bookTotalNumber"
                :current-page.sync="currentPage"
                @current-change="handleCurrentChange"
                :page-size="4"
            >
            </el-pagination>
        </el-row>
    </div>
</template>

<script>
    import qs from 'qs'
    export default {
        name: 'BookList',
        data() {
            return {
                bookList:[],
                search: '',
                showDeleteDialog: false,
                showUpdateDialog: false,
                operateBook: {},
                currentPage: 1,
                bookTotalNumber: 0
            }
        },
        methods: {
            handleEdit(index, row) {
                this.operateBook = row
                this.showUpdateDialog = true
            },
            handleDelete(index, row) {
                this.operateBook = row
                this.showDeleteDialog = true
            },
            deleteBook(){
                this.$axios.post('/book/deleteBook',qs.stringify({
                    id:this.operateBook.id
                })).then(
                    response => {
                        if(response.data > 0)
                        {
                            //加载书籍总数量bookTotalNumber
                            this.$axios.get('/book/getTotalNumber').then(
                                res => {
                                    this.bookTotalNumber = res.data
                                },
                                error => {
                                    console.log(error.message)
                                }
                            )

                             //加载当前页的书籍列表
                            this.$axios.post(`/book/getBookListByBookName/${this.currentPage}/4`, qs.stringify({ bookName:this.search.trim()})).then(
                                res => {
                                    this.bookList = res.data
                                },
                                error => {
                                    console.log(error.message)
                                }
                            )

                            this.$notify({
                                title: '成功',
                                message: `删除书籍${this.operateBook.bookName}成功`,
                                type: 'success'
                            });
                            this.showDeleteDialog = false
                        }
                    },
                    error => {
                        console.log(error.message)
                    }
                )
            },
            updateBook(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$axios.post('/book/updateBook' ,qs.stringify(this.operateBook)).then(
                            response => {
                                this.$notify({
                                    title: '成功',
                                    message: `修改书籍${this.operateBook.bookName}成功`,
                                    type: 'success'
                                });
                                this.showUpdateDialog = false
                            },
                            error => {
                                console.log(error.message)
                            }
                        )
                    } else {
                    return false;
                    }
                });
            },
            resetForm(formName) {
                this.form.price = 0
                this.$refs[formName].resetFields();
            },
             handleClose(done) {
                this.$confirm('你确定放弃修改吗?')
                .then(_ => {
                    done();
                })
                .catch(_ => {});
            },
            showSearchBook()
            {
                this.$axios.post('/book/getBookByBookNameNumber', qs.stringify({ bookName:this.search.trim()})).then(
                    response => {
                        this.bookTotalNumber = response.data
                    },
                    error => {
                        console.log(error.message)
                    }
                )

                //加载当前页的书籍列表
                this.$axios.post(`/book/getBookListByBookName/${this.currentPage}/4`, qs.stringify({ bookName:this.search.trim()})).then(
                    response => {
                        this.bookList = response.data
                    },
                    error => {
                        console.log(error.message)
                    }
                )
            },
            handleCurrentChange()
            {
                if(this.search.trim() === '')
                {
                    //加载书籍总数量bookTotalNumber
                    this.$axios.get('/book/getTotalNumber').then(
                        response => {
                            this.bookTotalNumber = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )

                    //加载当前页的书籍列表
                    this.$axios.get(`/book/page/${this.currentPage}/4`).then(
                        response => {
                            console.log(response.data)
                            this.bookList = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )
                }
                else{
                    this.$axios.post('/book/getBookByBookNameNumber', qs.stringify({ bookName:this.search.trim()})).then(
                        response => {
                            this.bookTotalNumber = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )

                    //加载当前页的书籍列表
                    this.$axios.post(`/book/getBookListByBookName/${this.currentPage}/4`, qs.stringify({ bookName:this.search.trim()})).then(
                        response => {
                            this.bookList = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )
                }
            }
        },
        mounted() {
        
            //加载书籍总数量bookTotalNumber
            this.$axios.get('/book/getTotalNumber').then(
                response => {
                    this.bookTotalNumber = response.data
                },
                error => {
                    console.log(error.message)
                }
            )

            //加载当前页的书籍列表
            this.$axios.get(`/book/page/${this.currentPage}/4`).then(
                response => {
                    console.log(response.data)
                    this.bookList = response.data
                },
                error => {
                    console.log(error.message)
                }
            )
        },
    }
</script>

<style scoped>
    #bookList{
        margin-top: 5px;
        border: 2px solid #DCDFE6;
        border-radius: 5px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }

    h2{
        margin: 20px auto;
        text-align: center;
        color: #409EFF;
        font-size: 24px;
    }

    .el-table{
        margin-left: 10px;
    }

     .el-pagination{
        margin-right: 50px;
        margin-top: 20px;
        margin-bottom: 60px;
    }
</style>