import Vue from 'vue'
import Vuex from 'vuex'

// 使用Vuex插件
Vue.use(Vuex)

const actions = {}

const mutations = {
    // 退出登录
    LOG_OUT(state)
    {
        sessionStorage.clear()
        state.username = ''
        state.marketId = ''
        state.role = ''
        state.badgeHide = true
        state.marketTotalNumber = 0
    },
    //添加书籍至购物车，更新总数
    ADD_MARKET_TOTAL_NUMBER(state,value)
    {
        state.marketTotalNumber += value
    },
    SUB_MARKET_TOTAL_NUMBER(state,value)
    {
        state.marketTotalNumber -= value
    }
}

const state = {
    username: '',
    marketId: '',
    role: '',
    // 是否隐藏购物车总数标识
    badgeHide: true,
    marketTotalNumber: 0
}


// Vuex模块化使用
export default new Vuex.Store({
    actions,
    mutations,
    state
})