<template>
  <div id="register">
    <el-form ref="form" :model="form" size="medium">
      <h2 class="title">用户注册</h2>
      <el-row type="flex" justify="center" align="middle">
        <el-col :span="6">
          <el-form-item
            label="账号"
            prop="username"
            :rules="[
              { required: true, message: '账号不能为空'}
            ]"
          >
            <el-input v-model="form.username"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row type="flex" justify="center" align="middle">
        <el-col :span="6">
          <el-form-item
            inline-message="true"
            label="密码"
            prop="password"
            :rules="[
              { required: true, message: '密码不能为空'}
            ]"
          >
            <el-input type="password" v-model="form.password"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row type="flex" justify="center" align="middle">
        <el-col :span="6">
          <el-form-item
            inline-message="true"
            label="确认密码"
            prop="passwordAgain"
            :rules="[
              { required: true, message: '请再次输入密码'}
            ]"
          >
            <el-input type="password" v-model="form.passwordAgain"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      

      <el-form-item>
        <el-row type="flex" justify="center" align="middle">
            <el-button type="primary" @click="register('form')">注册</el-button>
            <el-button @click="resetForm('form')">重置</el-button>
        </el-row>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
    import qs from 'qs'
    import {nanoid} from 'nanoid'
    export default {
        name: 'Register',
        data() {
          return {
              form: {
                  username: '',
                  password: '',
                  passwordAgain: '',
              
              }
          }
        },
        methods: {
          register(formName) {
              this.$refs[formName].validate((valid) => {
                if (valid) {
                    if(this.form.password === this.form.passwordAgain)
                    {
                      this.$axios.post('/user/register' ,qs.stringify({
                          username: this.form.username,
                          password: this.form.password,
                          marketId: nanoid()
                      })).then(
                          response => {
                            if(response.data === '')
                            {
                              this.$notify({
                                  title: '成功',
                                  message: `注册用户${this.form.username}成功`,
                                  type: 'success'
                              });
                              this.$router.push('/login')
                            }
                            else
                            {
                              this.$message({
                                showClose: true,
                                message: `${response.data}`,
                                type: 'error',
                                duration: '2000',
                                center: true
                              });
                            }
                          },
                          error => {
                            console.log(error.message)
                          }
                      )
                    }
                    else
                    {
                        this.$message({
                          showClose: true,
                          message: '两次输入密码不一致!!!',
                          type: 'error',
                          duration: '2000',
                          center: true
                        });
                    }
                } else {
                  return false;
                }
              });
          },
          resetForm(formName) {
              this.$refs[formName].resetFields();
          }
        }
    }
</script>

<style scoped>
  h2{
    margin-top: 80px;
    text-align: center;
    color: #409EFF;
    font-size: 24px;
  }

</style>