<template>
  <div id="login">
    <el-form ref="form" :model="form" size="medium">
      <h2 class="title">用户登录</h2>
      <el-row type="flex" justify="center" align="middle">
        <el-col :span="6">
          <el-form-item
            label="账号"
            prop="username"
            :rules="[
              { required: true, message: '账号不能为空'}
            ]"
          >
            <el-input v-model="form.username"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row type="flex" justify="center" align="middle">
        <el-col :span="6">
          <el-form-item
            inline-message="true"
            label="密码"
            prop="password"
            :rules="[
              { required: true, message: '密码不能为空'}
            ]"
          >
            <el-input type="password" v-model="form.password"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      

      <el-form-item>
        <el-row type="flex" justify="center" align="middle">
            <el-button type="primary" @click="login('form')">登录</el-button>
            <el-button @click="resetForm('form')">重置</el-button>
        </el-row>
      </el-form-item>
      <el-row type="flex" justify="center" align="middle">
        <el-link class="go" :underline="false" href="/register">暂无用户? 去注册</el-link>
      </el-row>
      <el-row type="flex" justify="center" align="middle">
        <el-link :underline="false" href="/findPassword">找回密码</el-link>
      </el-row>
    </el-form>
  </div>
</template>

<script>
  import qs from 'qs'
  export default {
      name: 'Login',
      data() {
          return {
              form: {
                  username: '',
                  password: '',
              }
          }
      },
      methods: {
          login(formName) {
              this.$refs[formName].validate((valid) => {
                if (valid) {
                  this.$axios.post('/user/login',
                    qs.stringify({
                      username: this.form.username,
                      password: this.form.password
                    })).then(
                        response => {
                          if(response.data)
                          {
                            //存储用户名到session和Vuex中
                            sessionStorage.setItem('username', this.form.username)
                            this.$store.state.username = response.data.username
                            this.$store.state.marketId = response.data.marketId
                            this.$store.state.role = response.data.role
                            this.$store.state.badgeHide = false

                            // 加载购物车
                            this.$axios.post('/market/getMarketTotalNumber', qs.stringify({
                                marketId: this.marketId
                            })).then(
                                response => {
                                    this.$store.state.marketTotalNumber = response.data
                                },
                                error => {
                                    console.log(error.message)
                                }
                            )

                            this.$router.push('/')
                          }
                          else
                          {
                            this.$message({
                              showClose: true,
                              message: `用户名或密码错误`,
                              type: 'error',
                              duration: '2000',
                              center: true
                            });
                          }
                        },
                        error => {
                          console.log(error.message)
                        });
                } else {
                  return false;
                }
              });
          },
          resetForm(formName) {
              this.$refs[formName].resetFields();
          }
      }
  }
</script>

<style scoped>
  h2{
    margin-top: 110px;
    text-align: center;
    color: #409EFF;
    font-size: 24px;
  }

  .go{
    margin: 10px auto;
  }
</style>