<template>
    <el-row>
        <el-col :span="4">
            <el-menu
                class="el-menu-vertical-demo"
                background-color="#606266"
                text-color="#fff"
                active-text-color="#ffd04b"
            >
                <el-submenu 
                    index="1"
                >
                    <template slot="title">
                        <i class="el-icon-menu"></i>
                        <span>个人管理</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item @click="showPersonalInfo" index="1-1">个人信息</el-menu-item>
                        <el-menu-item @click="showUpdatePassword" index="1-2">修改密码</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                <el-submenu 
                    v-if="role === 'admin'"
                    index="2"
                >
                    <template slot="title">
                        <i class="el-icon-menu"></i>
                        <span>书籍管理</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item @click="showAddBook" index="2-1">新增书籍</el-menu-item>
                        <el-menu-item @click="showBookList" index="2-2">书籍列表</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                <el-menu-item index="3" @click="showOrderList">
                    <i class="el-icon-notebook-2"></i>
                    <span slot="title">订单管理</span>
                </el-menu-item>
            </el-menu>
        </el-col>
        <el-col :span="13" :offset="3">
            <router-view></router-view>
        </el-col>
    </el-row>
</template>

<script>
    import {mapState} from 'vuex'
    export default {
        name: 'BookLeftNav',
        computed:{
            ...mapState(['role']),
        },
        methods: {
            showPersonalInfo(){
                if(this.$route.path !== '/bookLeftNav/personalInfo')
                {
                    this.$router.push('/bookLeftNav/personalInfo')
                }
            },
            showUpdatePassword(){
                if(this.$route.path !== '/bookLeftNav/passwordUpdate')
                {
                    this.$router.push('/bookLeftNav/passwordUpdate')
                }
            },
            showAddBook(){
                if(this.$route.path !== '/bookLeftNav/addBook')
                {
                    this.$router.push('/bookLeftNav/addBook')
                }
            },
            showBookList(){
                if(this.$route.path !== '/bookLeftNav/bookList')
                {
                    this.$router.push('/bookLeftNav/bookList')
                }
            },
            showOrderList(){
                if(this.$route.path !== '/bookLeftNav/orderList')
                {
                    this.$router.push('/bookLeftNav/orderList')
                }
            }
        },
    }
</script>

