<template>
    <div id="personalInfo">
        <el-row type="flex" justify="center" align="middle">
            <h2 class="title">个人信息</h2>
        </el-row>
        <el-form ref="form" :model="form" label-width="80px" :hide-required-asterisk="true">
            <el-row type="flex" justify="center" align="middle">
                <el-col :span="12" :pull="2">
                    <el-form-item 
                        prop="username"
                        label="用户名"
                    >
                        <el-input v-model="form.username" :disabled="true"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="16">
                    <el-form-item 
                        prop="gender"
                        label="性别"
                    >
                        <el-radio v-model="form.gender" label="男">男</el-radio>
                        <el-radio v-model="form.gender" label="女">女</el-radio>
                    </el-form-item>
                </el-col>
            </el-row>
            
            <el-row type="flex" justify="center" align="middle">
                <el-col :span="16">
                    <el-form-item 
                        prop="birthday"
                        label="出生日期"
                    >
                        <el-date-picker
                            v-model="form.birthday"
                            type="date"
                            placeholder="选择日期"
                            value-format="yyyy-MM-dd"
                        >
                        </el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="12" :pull="2">
                    <el-form-item 
                        prop="phone"
                        label="手机号"
                    >
                        <el-input v-model="form.phone"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="16">
                    <el-form-item
                        prop="image"
                        label="头像"
                    >
                        <el-upload
                            class="upload-demo"
                            action="http://localhost:8088/user/upload"
                            list-type="picture"
                            :on-change="handleImage"
                            :on-success="handleImageSuccess"
                            :auto-upload="false"
                            ref="upload"
                            accept=".jpg, .jpeg, .png"
                            :show-file-list="false"
                        >
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2M</div>
                        </el-upload>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-image 
                style="width: 130px; height: 130px" 
                :src="url" 
                fit="contain"
            >
            </el-image>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="16">
                    <el-form-item
                        prop="autograph"
                        label="个性签名"
                    >
                        <el-input 
                            :autosize="{ minRows: 3, maxRows: 5}"
                            type="textarea" 
                            v-model="form.autograph"
                            maxlength="100"
                            show-word-limit
                        >
                        </el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-form-item>
                <el-row type="flex" justify="center" align="middle">
                    <el-col :span="10">
                        <el-button type="primary" @click="savePersonalInfo">保存</el-button>
                        <el-button @click="resetForm('form')">重置</el-button>
                    </el-col>
                </el-row>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import qs from 'qs'
    import {mapState} from 'vuex'
    export default {
        name: 'PersonalInfo',
        data() {
            return {
                form:{
                    username: '',
                    gender: '',
                    birthday: '',
                    phone: '',
                    autograph: '',
                    image: '',
                },
                url:''
            }
        },
        computed:{
            ...mapState(['username'])
        },
        methods: {
            savePersonalInfo(){
                //开始上传图片
                this.$refs.upload.submit()

                this.$axios.post('/user/updatePersonalInfo' , {
                    username: this.form.username,
                    gender: this.form.gender,
                    birthday: this.form.birthday,
                    phone: this.form.phone,
                    autograph: this.form.autograph,
                    image: "/images/" + this.form.image
                }).then(
                    response => {
                        this.$notify({
                            title: '成功',
                            message: response.data,
                            type: 'success',
                            duration: 2000
                        });
                    },
                    error => {
                        console.log(error.message)
                    }
                )
            },
            resetForm(formName) {
                this.$refs.upload.clearFiles()
                this.$refs[formName].resetFields();
                this.form.username = this.username
            },
            handleImage(res, file) {
                // console.log(res, file)
                const format = res.raw.type ==  'image/jpeg' || res.raw.type == 'image/jpg' || res.raw.type == 'image/png'
                const isLt2M = res.raw.size / 1024 / 1024 < 2
                if (!format || !isLt2M) {
                    this.$message({
                        message: '只能上传jpg, jpeg, png,且图片大小不能超过 2MB!',
                        type: 'warning',
                        duration: '2000',
                        center: true
                    });
                    file.splice(0, 1)
                }
                else
                {
                    this.url = res.url
                    this.form.image = res.name
                }
                if(file.length > 1)
                {
                    file.splice(0, 1)
                }
                return format && isLt2M
            },
            handleImageSuccess(response, file, fileList){
                //上传图片成功，删除图片显示
                fileList.splice(0, 1)
                return 
            }
        },
        mounted() {
            this.form.username = this.username

            this.$axios.post('/user/getUserInfo' ,qs.stringify({
                username: this.form.username
            })).then(
                response => {
                    this.form.gender = response.data.gender
                    this.form.birthday = response.data.birthday
                    this.form.phone = response.data.phone
                    this.form.autograph = response.data.autograph
                    this.form.image = response.data.image
                    this.url = response.data.image
                },
                error => {
                    console.log(error.message)
                }
            )
        },
    }
</script>

<style scoped>
    #personalInfo{
        margin-top: 5px;
        border: 2px solid #DCDFE6;
        border-radius: 5px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }

    .title{
        margin-top: 30px;
        margin-bottom: 20px;
        text-align: center;
        color: #409EFF;
        font-size: 24px;
    }

    .el-radio{
        margin-right: 80px;
    }

    .el-image {
        border-radius: 50%;
        position: absolute;
        right: 250px;
        top: 280px;
        overflow: hidden;
    }
</style>