<template>
    <div id="passwordUpdate">
        <el-form ref="form" :model="form" size="medium">
            <h2 class="title">修改密码</h2>
            <el-row type="flex" justify="center" align="middle">
                <el-col :span="12">
                    <el-form-item
                        inline-message="true"
                        label="旧密码"
                        prop="oldPassword"
                        :rules="[
                            { required: true, message: '旧密码不能为空'}
                        ]"
                    >
                        <el-input type="password" v-model="form.oldPassword"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="12">
                    <el-form-item
                        inline-message="true"
                        label="新密码"
                        prop="newPassword"
                        :rules="[
                            { required: true, message: '新密码不能为空'}
                        ]"
                    >
                        <el-input type="password" v-model="form.newPassword"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="12">
                    <el-form-item
                        inline-message="true"
                        label="确认密码"
                        prop="passwordAgain"
                        :rules="[
                            { required: true, message: '请再次输入密码'}
                        ]"
                    >
                        <el-input type="password" v-model="form.passwordAgain"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-form-item>
                <el-row type="flex" justify="center" align="middle">
                    <el-button type="primary" @click="updatePassword('form')">修改密码</el-button>
                    <el-button @click="resetForm('form')">重置</el-button>
                </el-row>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import qs from 'qs'
    import {mapState} from 'vuex'
    export default {
        name: 'PasswordUpdate',
        data() {
            return {
                form: {
                    oldPassword: '',
                    newPassword: '',
                    passwordAgain: '',
                }
            }
        },
        computed:{
            ...mapState(['username'])
        },
        methods: {
            updatePassword(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if(this.form.newPassword === this.form.passwordAgain)
                        {
                            this.$axios.post('/user/updatePassword' ,qs.stringify({
                                username: this.username,
                                oldPassword: this.form.oldPassword,
                                newPassword: this.form.newPassword
                            })).then(
                                response => {
                                    if(response.data === '')
                                    {
                                        this.$notify({
                                            title: '成功',
                                            message: `账号${this.username}密码修改成功`,
                                            type: 'success'
                                        });
                                        this.resetForm('form')
                                    }
                                    else
                                    {
                                        this.$message({
                                            showClose: true,
                                            message: `${response.data}`,
                                            type: 'error',
                                            duration: '2000',
                                            center: true
                                        });
                                    }
                                },
                                error => {
                                    console.log(error.message)
                                }
                            )
                        }
                        else
                        {
                            this.$message({
                                showClose: true,
                                message: '两次输入密码不一致!!!',
                                type: 'error',
                                duration: '2000',
                                center: true
                            });
                        }
                    } else {
                    return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style>
    #passwordUpdate{
        margin-top: 30px;
        border: 2px solid #DCDFE6;
        border-radius: 5px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }

    h2{
        margin-top: 40px;
        text-align: center;
        color: #409EFF;
        font-size: 24px;
    }
</style>