<template>
    <div id="addBook">
        <el-row type="flex" justify="center" align="middle">
            <h2 class="title">新增书籍</h2>
        </el-row>
        <el-form ref="form" :model="form" label-width="80px" :hide-required-asterisk="true">
            <el-row type="flex" justify="center" align="middle">
                <el-col :span="18">
                    <el-form-item 
                        prop="bookName"
                        label="图书名称"
                        :rules="[
                            { required: true, message: '请输入图书名称'},
                        ]"
                    >
                        <el-input v-model="form.bookName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="18">
                    <el-form-item 
                        prop="author"
                        label="作者"
                        :rules="[
                            { required: true, message: '请输入图书作者'},
                        ]"
                    >
                        <el-input v-model="form.author"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            
            <el-row type="flex" justify="center" align="middle">
                <el-col :span="18">
                    <el-form-item 
                        prop="price"
                        label="图书单价"
                    >
                        <el-slider
                            v-model="form.price"
                            show-input
                            :max="200"
                        >
                        </el-slider>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="18">
                    <el-form-item
                        prop="image"
                        label="图书相片"
                    >
                        <el-upload
                            class="upload-demo"
                            action="http://localhost:8088/book/upload"
                            list-type="picture"
                            :on-change="handleImage"
                            :on-success="handleImageSuccess"
                            :auto-upload="false"
                            ref="upload"
                            accept=".jpg, .jpeg, .png,"
                        >
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2M</div>
                        </el-upload>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="18">
                    <el-form-item
                        prop="description"
                        label="图书简介"
                        :rules="[
                            { required: true, message: '请输入图书简介'},
                        ]"
                    >
                        <el-input 
                            :autosize="{ minRows: 4, maxRows: 10}"
                            type="textarea" 
                            v-model="form.description"
                            maxlength="500"
                            show-word-limit
                        >
                        </el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row type="flex" justify="center" align="middle">
                <el-col :span="18">
                    <el-form-item>
                        <el-button type="primary" @click="addBook('form')">新增书籍</el-button>
                        <el-button @click="resetForm('form')">重置</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    import qs from 'qs'
    import {nanoid} from 'nanoid'
    export default {
        name:'AddBook',
        data() {
            return {
                form:{
                    bookName: '',
                    author: '',
                    price: 0,
                    image: '',
                    description: '',
                },
            }
        },
        methods: {
            addBook(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if(this.form.price === 0)
                        {
                            this.$message({
                                message: '请设置书籍的单价!',
                                type: 'warning',
                                duration: '2000',
                                center: true
                            });
                            return false
                        }

                        if(this.form.image === '')
                        {
                            this.$message({
                                message: '请上传书籍的图片!',
                                type: 'warning',
                                duration: '2000',
                                center: true
                            });
                            return false
                        }
                        //开始上传图片
                        this.$refs.upload.submit()

                        //新增书籍
                        this.$axios.post('/book/addBook' ,qs.stringify({
                            id:nanoid(),
                            bookName:this.form.bookName,
                            author:this.form.author,
                            image:"/images/" + this.form.image,
                            price:this.form.price,
                            description:this.form.description
                        })).then(
                            response => {
                                this.$notify({
                                    title: '成功',
                                    message: response.data,
                                    type: 'success',
                                    duration: 2000
                                });
                                //重置表单
                                this.resetForm('form')
                            },
                            error => {
                                console.log(error.message)
                            }
                        )
                    } else {
                        return false;
                    }
                });
            },
            resetForm(formName) {
                console.log(formName)
                this.form.price = 0
                this.form.image = ''
                this.$refs.upload.clearFiles()
                this.$refs[formName].resetFields();
            },
            handleImage(res, file) {
                // console.log(res, file)
                const format = res.raw.type ==  'image/jpeg' || res.raw.type == 'image/jpg' || res.raw.type == 'image/png'
                const isLt2M = res.raw.size / 1024 / 1024 < 2
                if (!format || !isLt2M) {
                    this.$message({
                        message: '只能上传jpg, jpeg, png,且图片大小不能超过 2MB!',
                        type: 'warning',
                        duration: '2000',
                        center: true
                    });
                    file.splice(0, 1)
                }
                else
                {
                    this.form.image = res.name
                }
                if(file.length > 1)
                {
                    file.splice(0, 1)
                }
                return format && isLt2M
            },
            handleImageSuccess(response, file, fileList){
                //上传图片成功，删除图片显示
                fileList.splice(0, 1)
                return 
            }
        }
    }
</script>

<style scoped>
    #addBook{
        margin-top: 5px;
        border: 2px solid #DCDFE6;
        border-radius: 5px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }

    h2{
        margin: 20px auto;
        text-align: center;
        color: #409EFF;
        font-size: 24px;
    }
</style>