<template>
    <div id="BookHeader">
        <nav class="navbar navbar-expand-lg navbar-light top">
            <div class="row col-lg-12">
                <router-link class="navbar-brand name" to="/"><span class="text-white">图书商城</span></router-link>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
    
                <router-link v-show="!username" class="nav-link text-light register" to="/register">注册</router-link>
                <router-link v-show="!username" class="nav-link text-light login" to="/login">登录</router-link>

                <!-- 购物车 -->
                <el-badge 
                    :value="marketTotalNumber" 
                    :max="99"
                    :hidden="badgeHide"
                    class="item"
                >
                    <i class="text-warning market" @click="showMarket">
                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-cart" viewBox="0 0 16 16">
                            <path d="M0 1.5A.5.5 0 0 1 .5 1H2a.5.5 0 0 1 .485.379L2.89 3H14.5a.5.5 0 0 1 .491.592l-1.5 8A.5.5 0 0 1 13 12H4a.5.5 0 0 1-.491-.408L2.01 3.607 1.61 2H.5a.5.5 0 0 1-.5-.5zM3.102 4l1.313 7h8.17l1.313-7H3.102zM5 12a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm7 0a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm-7 1a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm7 0a1 1 0 1 1 0 2 1 1 0 0 1 0-2z"/>
                        </svg>
                    </i>
                </el-badge>

                <el-drawer
                    title="购物车"
                    :visible.sync="marketVisible"
                    direction="rtl"
                    size="45%"
                    :append-to-body="true"
                    :before-close="handleClose"
                >
                    <el-table
                        :data="bookList"
                        stripe
                        style="width: 100%"
                        height="500"
                    >
                        <el-table-column
                            prop="bookName"
                            label="书名"
                            width="100"
                        >
                        </el-table-column>
                        <el-table-column
                            prop="image"
                            label="图片"
                            width="100"
                        >
                            <template slot-scope="scope">
                                <el-image
                                    style="width: 100px; height: 100px"
                                    :src="scope.row.image"
                                    fit="fill">
                                </el-image>
                            </template>
                        </el-table-column>
                        <el-table-column
                            prop="price"
                            label="单价"
                            width="70"
                        >
                        </el-table-column>
                        <el-table-column
                            label="数量"
                            width="200"
                        >
                            <template slot-scope="scope">
                                <el-input-number v-model="scope.row.number" @change="handleChange(scope)" size="small" :min="1"></el-input-number>
                            </template>
                        </el-table-column>
                        <el-table-column
                            label="操作"
                            width="100"
                        >
                            <template slot-scope="scope">
                                <el-button @click="handleDelete(scope)" type="danger">移除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-tag 
                        :hit="true"
                        class="totalMoney"
                    >
                        总金额: {{getTotalMoney}}
                    </el-tag>
                    <el-button @click="pay" class="pay" type="primary">支付</el-button>
                    <el-dialog
                        title="提示"
                        :visible.sync="showDelete"
                        width="30%"
                        :append-to-body="true"
                    >
                        <span>你确定要移除{{deleteBookSave.bookName}}吗?</span>
                        <span slot="footer" class="dialog-footer">
                            <el-row type="flex" justify="center" align="middle">
                                <el-button @click="showDelete = false">取 消</el-button>
                                <el-button type="primary" @click="deleteBook">确 定</el-button>
                            </el-row>
                        </span>
                    </el-dialog>
                </el-drawer>


                <!-- 用户中心 -->
                <div
                    v-show="username"
                    class="user"
                >
                    <router-link class="userIcon text-light" to="/bookLeftNav">
                        <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" class="bi bi-person-circle" viewBox="0 0 16 16">
                            <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0z"/>
                            <path fill-rule="evenodd" d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8zm8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1z"/>
                        </svg>
                    </router-link>
                </div>
                <button @click="showLogOut = true" v-show="username" class="btn btn-secondary btn-sm exit">退出</button>
            </div>
        </nav>
        <el-dialog
            title="提示"
            :visible.sync="showLogOut"
            width="30%"
        >
            <span>你确定要退出登录吗?</span>
            <span slot="footer" class="dialog-footer">
            <el-row type="flex" justify="center" align="middle">
                    <el-button @click="showLogOut = false">取 消</el-button>
                    <el-button type="primary" @click="handleLogOut">确 定</el-button>
                </el-row>
            </span>
        </el-dialog>
        <router-view></router-view>
    </div>
</template>

<script>
    import {nanoid} from 'nanoid'
    import qs from 'qs'
    import {mapState, mapMutations} from 'vuex'
    export default {
        name:'BookHeader',
        data() {
            return {
                marketVisible: false,
                showLogOut: false,
                showDelete: false,
                bookList:[],
                deleteBookSave:{},
                updateBookList:[],
            }
        },
        computed:{
            ...mapState(['username', 'badgeHide', 'marketId', 'marketTotalNumber']),
            getTotalMoney(){
                var totalMoney = 0;
                this.bookList.forEach(item => {
                    totalMoney += item.price * item.number
                })
                return totalMoney
            }
        },
        methods:{
            ...mapMutations({logOut:'LOG_OUT', subMarketTotalNumber:'SUB_MARKET_TOTAL_NUMBER'}),
            showMarket(){
                if(this.$store.state.username !== ''){
                    this.$axios.post('/market/getMarketListByMarketId', qs.stringify({
                        marketId: this.marketId
                    })).then(
                        response => {
                            response.data.forEach((item) => {
                                this.$axios.post('/book/getBookById', qs.stringify({
                                    id: item.bookId
                                })).then(
                                    res => {
                                        res.data.number = item.number
                                        this.bookList.unshift(res.data)
                                    },
                                     error => {
                                        console.log(error.message)
                                    }
                                )
                            });

                            this.$store.state.badgeHide = false
                            this.marketVisible = true
                        },
                        error => {
                            console.log(error.message)
                        }
                    )
                }
                else
                {
                    this.$message({
                        message: '请先登录再查看购物车!',
                        type: 'warning',
                        duration: '2000',
                        center: true
                    });
                    if(this.$route.path !== '/login')
                    {
                        this.$router.push('/login')
                    }
                }
            },
            handleChange(scope) {
                if(this.updateBookList.indexOf(scope.row) === -1)
                {
                    this.updateBookList.unshift(scope.row)
                }
            },
            //关闭购物车后更新数据
            handleClose(done){
                this.updateBookList.forEach((item) => {
                    this.$axios.post('/market/updateMarket',qs.stringify({
                        marketId: this.marketId,
                        bookId: item.id,
                        number: item.number
                    })).then(
                        response => {
                            console.log(response.data)
                        },
                        error => {
                            console.log(error.message)
                        }
                    )
                })
                this.bookList = []
                this.updateBookList = []
                done()
            },
            handleDelete(scope){
                console.log(scope.row)
                this.deleteBookSave = scope.row
                this.showDelete = true
            },
            deleteBook(){
                this.$axios.post('/market/deleteFromMarket', qs.stringify({
                    marketId: this.marketId,
                    bookId: this.deleteBookSave.id
                })).then(
                    response => {
                        this.bookList.forEach((item, index) =>{
                            if(item === this.deleteBookSave)
                            {
                                this.bookList.splice(index, 1)
                            }
                        })
                        this.$notify({
                            title: '成功',
                            message: response.data,
                            type: 'success'
                        });
                        this.showDelete = false
                        this.subMarketTotalNumber(this.deleteBookSave.number)
                    },
                    error => {
                        console.log(error.message)
                    }
                )
            },
            handleLogOut(){
                this.showLogOut = false
                this.logOut()
                if(this.$route.path !== '/login')
                {
                    this.$router.push('/login')
                }
                this.$notify({
                    title: '成功',
                    message: "注销成功!",
                    type: 'success'
                });
            },
            pay(){
                if(this.bookList.length !== 0)
                {
                    const orderId = nanoid()
                    this.$axios.post('/order/createOrder', {
                        orderId: orderId,
                        date: this.$getCurrentDate(),
                        cost: this.getTotalMoney,
                        marketId: this.marketId
                    }).then(
                        response => {
                            this.$notify({
                                title: '成功',
                                message: response.data,
                                type: 'success'
                            });
                        },
                        error => {
                            console.log(error.message)
                        }
                    )

                    this.bookList.forEach((item) => {
                        this.$axios.post('/orderDetails/addOrderDetails', qs.stringify({
                            bookId: item.id,
                            orderId: orderId,
                            number: item.number
                        })).then(
                            response => {
                            console.log(response.data)
                            },
                            error => {
                                console.log(error.message)
                            }
                        )
                    })

                    //清空购物车
                    this.$axios.post('/market/deleteFromMarket', qs.stringify({
                        marketId: this.marketId,
                    })).then(
                        response => {
                            console.log(response.data)
                        },
                        error => {
                            console.log(error.message)
                        }
                    )

                    this.bookList.splice(0, this.bookList.length)
                    this.$store.state.marketTotalNumber = 0
                }
                else
                {
                    this.$message({
                        message: '请先将书籍添加到购物车后在支付!',
                        type: 'warning',
                        duration: '2000',
                        center: true
                    });
                }
            }
        },
        mounted() {
            //加载购物车书籍总数目
            this.$axios.post('/market/getMarketTotalNumber', qs.stringify({
                marketId: this.marketId,
            })).then(
                response => {
                    this.$store.marketTotalNumber = response.data
                },
                error => {
                    console.log(error.message)
                }
            )
        },
    }
</script>

<style scoped>
    img {
        width: 200px;
    }

    svg {
        margin-top: 30%;
    }

    .top{
        position: sticky;
        top: 0px;
        z-index: 2;
        background-color: rgb(27, 26, 26);
    }

    .top .name{
        margin-left: 8px;
        margin-right: 850px;
    }

    .top .login{
        margin-right: 10px;
    }
    .top .market{
        margin-top: 2px;
        margin-right: 20px;
    }

    .top .user{
        margin-top: 1px;
        margin-left: 5px;
    }

    .top .exit{
        margin-top: 5px;
        position: absolute;
        right: 0;
    }

    .totalMoney{
        margin-left: 20px;
        margin-top: 25px;
        width: 140px;
        height: 30px;
        text-align: center;
        font-size: 16px;
    }

    .pay {
        position: absolute;
        bottom: 15px;
        right: 30px;
    }
</style>