<template>
    <div id="orderList">
        <el-row type="flex" justify="center" align="middle">
            <h2 class="title">订单列表</h2>
        </el-row>

        <div class="content">
            <el-table
                :data="orderList"
                stripe
                style="width: 95%"
                :default-sort = "{prop: 'date', order: 'descending'}"
                height="556"
            >
                <el-table-column
                    prop="orderId"
                    label="订单号"
                    width="120"
                >
                </el-table-column>
                <el-table-column
                    prop="date"
                    label="日期"
                    sortable
                    width="150"
                >
                </el-table-column>
                <el-table-column
                    prop="cost"
                    label="总金额"
                    width="100"
                >
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="200"
                >
                    <template slot-scope="scope">
                        <el-dialog
                            title="订单详情"
                            :visible.sync="dialogVisible"
                            width="48%"
                            :before-close="clearBookList"
                        >
                            <el-table :data="bookList">
                                <el-table-column width="150" property="id" label="编号"></el-table-column>
                                <el-table-column width="100" property="bookName" label="书名"></el-table-column>
                                <el-table-column width="100" property="author" label="作者"></el-table-column>
                                <el-table-column width="100" property="price" label="单价"></el-table-column>
                                <el-table-column width="100" property="number" label="数量"></el-table-column>
                            </el-table>
                        </el-dialog>
                            
                        <el-button
                            size="mini"
                            type="primary"
                            @click="showDetails(scope.row.orderId)"
                        >
                            详情
                        </el-button>

                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                        >
                            删除
                        </el-button>
                    </template>
                </el-table-column> 
            </el-table>

            <el-dialog
                title="提示"
                :visible.sync="showDeleteDialog"
                width="30%"
                :center="true"
            >
                <span>你确定要删除订单吗?</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="showDeleteDialog = false">取 消</el-button>
                    <el-button type="primary" @click="deleteOrder">确 定</el-button>
                </span>
            </el-dialog>

            <el-row type="flex" justify="center" align="middle">
                <el-pagination
                    background
                    layout="prev, pager, next"
                    :total="orderTotalNumber"
                    :current-page.sync="currentPage"
                    @current-change="handleCurrentChange"
                    :page-size="7"
                >
                </el-pagination>
            </el-row>
        </div>
    </div>
</template>

<script>
    import qs from 'qs'
    import {mapState} from 'vuex'
    export default {
        name: 'OrderList',
        data() {
            return {
                orderList:[],
                bookList:[],
                dialogVisible: false,
                search: '',
                showDeleteDialog: false,
                operateOrder:{},
                currentPage: 1,
                orderTotalNumber: 0
            }
        },
        computed:{
            ...mapState(['marketId']),
        },
        methods: {
            handleDelete(index, row) {
                this.operateOrder = row
                this.showDeleteDialog = true
            },
            deleteOrder(){
                this.$axios.post('/order/deleteOrder', qs.stringify({
                    orderId: this.operateOrder.orderId
                })).then(
                    res => {
                        //加载订单总数量orderTotalNumber
                    this.$axios.post('/order/getTotalNumber',qs.stringify({
                        marketId: this.marketId
                    })).then(
                        response => {
                            this.orderTotalNumber = response.data
                        },
                        error => {
                            console.log(error.message)
                        }
                    )

                    this.$axios.post(`/order/getOrderListByMarketId/${this.currentPage}/7`, qs.stringify({
                        marketId: this.marketId,
                    })).then(
                        response => {
                            if(!response.data instanceof Array)
                            {

                                this.orderList.unshift(response.data[0])
                            }
                            else
                            {
                                this.orderList = response.data
                            }
                        },
                        error => {
                            console.log(error.message)
                        }
                    )
                        console.log(res.data)
                    },
                    error => {
                        console.log(error.message)
                    }
                )

                this.$axios.post('/orderDetails/deleteOrderDetails', qs.stringify({
                    orderId: this.operateOrder.orderId
                })).then(
                    res => {
                        console.log(res.data)
                    },
                    error => {
                        console.log(error.message)
                    }
                )

                this.$notify({
                    title: '成功',
                    message: `删除订单成功`,
                    type: 'success'
                });
                this.showDeleteDialog = false
            },
            showDetails(orderId){
                this.$axios.post('/orderDetails/getOrderDetailsByOrderId', qs.stringify({
                    orderId: orderId,
                })).then(
                    response => {
                        var list = []
                        if(!response.data instanceof Array)
                        {
                            list.unshift(response.data[0])
                        }
                        else
                        {
                            list = response.data
                        }
                        console.log(list)
                        list.forEach((item) => {
                            const bookId = item.bookId
                            const number = item.number
                            this.$axios.post('/book/getBookById', qs.stringify({
                                id: bookId,
                            })).then(
                                res => {
                                    this.bookList.unshift({
                                        id: bookId,
                                        bookName: res.data.bookName,
                                        author: res.data.author,
                                        price: res.data.price,
                                        number: number
                                    })
                                },
                                error => {
                                    console.log(error.message)
                                }
                            )
                        })
                        this.dialogVisible = true
                    },
                    error => {
                        console.log(error.message)
                    }
                )
            },
            clearBookList(done)
            {
                this.bookList = []
                done()
            },
            handleCurrentChange()
            {
                //加载当前页的书籍列表
                this.$axios.post(`/order/getOrderListByMarketId/${this.currentPage}/7`,qs.stringify({
                    marketId: this.marketId
                })).then(
                    response => {
                        console.log(response.data)
                        this.orderList = response.data
                    },
                    error => {
                        console.log(error.message)
                    }
                )
            },
        },
        mounted() {
            //加载订单总数量orderTotalNumber
            this.$axios.post('/order/getTotalNumber',qs.stringify({
                marketId: this.marketId
            })).then(
                response => {
                    this.orderTotalNumber = response.data
                },
                error => {
                    console.log(error.message)
                }
            )

            this.$axios.post(`/order/getOrderListByMarketId/${this.currentPage}/7`, qs.stringify({
                marketId: this.marketId,
            })).then(
                response => {
                    if(!response.data instanceof Array)
                    {

                        this.orderList.unshift(response.data[0])
                    }
                    else
                    {
                        this.orderList = response.data
                    }
                },
                error => {
                    console.log(error.message)
                }
            )
        },
    }
</script>

<style scoped>
    #orderList{
        margin-top: 5px;
        border: 2px solid #DCDFE6;
        border-radius: 5px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }

    h2{
        margin: 20px auto;
        text-align: center;
        color: #409EFF;
        font-size: 24px;
    }

    .content{
        margin-left: 50px;
        margin-bottom: 20px;
    }

    .el-button{
        margin-right: 10px;
    }

    .el-pagination{
        margin-right: 30px;
        margin-top: 20px;
    }
</style>